<template>
  <div class="plant-change content">
    <div class="title">
      {{ $t('finace.out.part1') }} 
    </div>
    <div class="coin-list clearfix content-pad">
      <el-select v-model="selectCoinID" class="coin pull-left" @change="handleSelect">
        <el-option
          v-for="item in selectList"
          :key="item.currency_id"
          :label="item.currency_mark"
          :value="item.currency_id" v-if="item.is_allow_recharge == 1">
        </el-option>
      </el-select>
      <div class="pull-left currency_mark">{{ $t('finace.out.part2') }} {{ coinDetail.currency_mark }}: {{ coinDetail.num }}</div>
			<div class="pull-left currency_mark">{{ $t('finace.out.part3') }} {{ coinDetail.currency_mark }}: {{ coinDetail.forzen_num }}</div>
    </div>
    <el-form :model="ruleForm" ref="ruleForm" :rules="rules" label-width="130px" label-position="left" class="plant-form">
        <el-form-item :label="$t('finace.out.part4')" prop="turn_out_address">
          <el-input  auto-complete="off" :placeholder="$t('finace.out.part5')" v-model.trim="ruleForm.turn_out_address"></el-input> 
        </el-form-item>
      <!-- </el-form-item> -->
      <el-form-item :label="$t('finace.out.part6')" prop="turn_out_num">
        <el-input type="text" auto-complete="off"  v-model.trim="ruleForm.turn_out_num" @input="changeByFabi('turn_out_num')"></el-input>
      </el-form-item>
      
      <el-form-item :label="$t('finace.out.part7')" >
        <div class="c-violet" v-if="coinDetail.fee_tibi_mod == 2">{{ coinDetail.currency_tibi_fee }} {{ coinDetail.currency_mark }}</div> 
        <div class="c-violet" v-else>{{ coinDetail.currency_tibi_fee }}%</div> 
      </el-form-item>
      <el-form-item :label="$t('finace.out.part8')" prop="turn_out_account" >
        <el-input  auto-complete="off" disabled v-model.trim="ruleForm.turn_out_account"></el-input> 
      </el-form-item>
      <el-form-item :label="$t('form.tradePwd')" prop="trade_password">
        <el-input type="password" auto-complete="off" :placeholder="$t('plant.form.pwdPlaceholder')" v-model.trim="ruleForm.trade_password"></el-input> 
      </el-form-item>
      <el-form-item :label="$t('plant.form.googleVerifyCode')" prop="pass_double" v-if="is_set_double_certification">
        <el-input type="text" auto-complete="off" :placeholder="$t('plant.form.googleVerifyCodePlaceholder')" v-model.trim="ruleForm.pass_double"></el-input>
      </el-form-item>
      <el-form-item :label="$t('plant.form.verifyCode')" prop="sms_verify" class="verify">
        <el-input type="text" auto-complete="off"  v-model.trim="ruleForm.sms_verify"></el-input><el-button class="get-verify" @click="sendRequestSms($event)" :disabled="!validCode">{{ validTip }}</el-button>
      </el-form-item>
      <el-form-item>
        <el-button type="primary"  @click.native="submitForm('ruleForm')" class="sure-change">{{ $t('finace.out.part9') }}</el-button>
      </el-form-item>
    </el-form>

    <div class="invite-rules">
      <div style="font-size: 13px;">{{ this.$t('finace.out.part10') }}</div>
      <p class="gray">{{ this.$t('finace.out.part11') }} {{ coinDetail.currency_mark }} {{ $t('finace.out.part12') }}</p>
      <p class="gray">{{ this.$t('finace.out.part13') }} {{ coinDetail.currency_mark }} {{ $t('finace.out.part31') }}</p>
      <p class="gray">{{ this.$t('finace.out.part14') }} {{ coinDetail.currency_tibi_fee }}%{{ $t('add.dot') }}</p>
    </div>

    <div class="invite-table">
      <div class="title" style="font-size: 16px;">{{ $t('finace.out.part15') }}</div>
        <el-table
        :data="tableData" class="data-table">
          <el-table-column :label="$t('finace.out.part16')" width="180">
            <template slot-scope="scope">
              <div class="time" style="color: #a9afbb">{{ scope.row.tibi_time }}</div>
            </template>
          </el-table-column>
          <el-table-column :label="$t('finace.out.part17')">
            <template slot-scope="scope">
              <div  class="wrap" :title="scope.row.tibi_url">{{ scope.row.tx_id }}</div>
            </template>
          </el-table-column>
          <el-table-column :label="$t('finace.out.part18')">
            <template slot-scope="scope">
              <div  class="wrap" :title="scope.row.tibi_url">{{ scope.row.tibi_url }}</div>
              
            </template>
          </el-table-column>
          <el-table-column :label="$t('finace.out.part19')">
            <template slot-scope="scope">
              {{ scope.row.actual_num }}
            </template>
          </el-table-column>
          <el-table-column :label="$t('finace.out.part20')">
            <template slot-scope="scope">
              {{ scope.row.actual_money }}
            </template>
          </el-table-column>
          <el-table-column prop="tibi_status" :label="$t('finace.out.part21')" 
            :filters="[{ text: $t('finace.out.part22'), value: 1 },{ text: $t('finace.out.part23'), value: 2 },{ text: $t('finace.out.part24'), value: 3 }, { text: $t('finace.out.part25'), value: 4 }]" :filter-method="filterTag1" filter-placement="bottom-end">
            <template slot-scope="scope">
              <!-- <div v-if="scope.row.tibi_status === -1" >全部</div> -->
              <div v-if="scope.row.tibi_status == 1" >{{ $t('finace.out.part22') }}</div>
              <div v-if="scope.row.tibi_status == 2" >{{ $t('finace.out.part23') }}</div>
              <div v-if="scope.row.tibi_status == 3" >{{ $t('finace.out.part24') }}</div>
              <div v-if="scope.row.tibi_status == 4" >{{ $t('finace.out.part25') }}</div>
              </template>
            </el-table-column>
          <el-table-column :label="$t('finace.out.part26')">
            <template slot-scope="scope">
              <div class="swicth-btn">
                <a href="javascript:void(0)" v-if="scope.row.tibi_status == 1" class="cancel"  @click="handleRevoke(scope.row.tibi_id)">{{ $t('finace.out.part27') }}</a>
              </div>
            </template>
          </el-table-column>
      </el-table>
      <el-pagination
        background
        layout="total, prev, pager, next, jumper"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pageOptions.page"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="pageOptions.per_page_num"
        :total="totalCount" class="page">
      </el-pagination>
    </div>
  </div>
</template>
<script>
  import md5 from 'js-md5'
  // import { getAdAboutList } from '@/api/trade'
  import { sendRequestSms } from '@/api/loginModule'
  import { checkToken } from '@/api/user'
  import { Pfp, sixNumber } from '@/utils/validate'

  import { financeCenter } from '@/api/orderManage'
  import { assetTurnOutToInfo, memberAssetOutTo, assetTurnOutToList, cancelAssetTurnOut } from '@/api/assetManage'
  
  export default {
    data() {
      const validateNumber = (rule, value, callback) => {
        if (!Pfp(value)) {
          callback(new Error(this.$t('errorTips.formatError')))
          // callback(new Error('输入值格式不正确！'))
        }
        callback()
      }
      const validateTradePwd = (rule, value, callback) => {
        if (!sixNumber(value)) {
          callback(new Error(this.$t('errorTips.tradePwdError')))
          // callback(new Error('交易密码必须为6位纯数字！'))
        }
        callback()
      }
      return {
        coinDetail: {},
        coinList: null,
        selectCoinID: 0,
        selectList: [],
        // 分页选项
        pageOptions: {
          per_page_num: 10,
          page: 1
        },
        totalCount: 0,
        tableData: null,

        ruleForm: {
          turn_out_address: '',
          turn_out_num: null,
          trade_password: null,
          sms_verify: '',
          pass_double: '',
          turn_out_account: 0
        },
        canUse: '',
        canUseVisible: false,
        finaceList: [],
        phone: '',
        validCode: true,
        is_set_double_certification: false,
        validState: true,
        validTip: this.$t('verifyCode.title'),
        timeInterval: null,
        rules: {
          turn_out_num: [
            { required: true, message: this.$t('plant.tips.num'), trigger: 'blur' },
            { validator: validateNumber, trigger: 'blur' }
          ],
          sms_verify: [
            { required: true, message: this.$t('plant.tips.verifyCode'), trigger: 'blur' }
          ],
          trade_password: [
            { required: true, message: this.$t('plant.tips.tradePwd'), trigger: 'blur' },
            { validator: validateTradePwd, trigger: 'blur' }
          ],
          turn_out_address: [
            { required: true, message: this.$t('finace.out.part28'), trigger: 'blur' }
          ],
          pass_double: [
            { required: true, message: this.$t('plant.tips.googleVerify'), trigger: 'blur' }
          ]
        }
      }
    },
    created() {
      this.getPhoneNum()
      this.getSelectList()
      this.getUserInfo()
    },
    methods: {
      handleSelect() {
        this.getTransferOutList()
        this.getAssetTurnInTo()
        this.$refs['ruleForm'].resetFields()
        window.location.href = window.location.href.split('?')[0] + '?currency_id=' + this.selectCoinID
      },
      filterTag1(value, row) {
        return row.tibi_status === value
      },
      filterHandler(value, row, column) {
        const property = column['property']
        return row[property] === value
      },
      handleSizeChange(val) {
        this.pageOptions.page = val
        this.getmyAdList()
      },
      handleCurrentChange(val) {
        this.pageOptions.page = val
        this.getmyAdList()
      },
      handleRevoke(orders_id) {
        const data = {
          'tibi_id': orders_id,
          'currency_id': this.selectCoinID
        }
        cancelAssetTurnOut(data).then(res => {
          if (res.status !== 1000) {
            this.$message.error(this.$t('finace.out.part29'))
            return false
          }
          this.$message.success(this.$t('finace.out.part30'))
          this.getTransferOutList()
        }).catch(() => {
        })
      },
      getSelectList() {
        financeCenter().then(res => {
          if (res.status !== 1000) {
            this.$message.error(res.msg)
          }
          this.selectList = [...res.data.normal_currency]
          if (this.$route.query.currency_id) {
            this.selectCoinID = parseFloat(this.$route.query.currency_id)
          } else {
            this.selectCoinID = this.selectList[0].currency_id
          }
          this.getTransferOutList()
          this.getAssetTurnInTo()
        }).catch(() => {
        })
      },
      getAssetTurnInTo() {
        const data = {
          'currency_id': this.selectCoinID
        }
        assetTurnOutToInfo(data).then(res => {
          console.log(res)
          if (res.status !== 1000) {
            this.$message.error(res.msg)
            return false
          }
          this.coinDetail = res.data
          this.getTransferOutList()
        }).catch(() => {
        })
      },
      changeByFabi(str) {
        if (/.{1}/.test(this.ruleForm[str])) {
          if (!(/^[0-9]*.[0-9]{0,5}$/).test(this.ruleForm[str])) {
            this.ruleForm[str] = this.ruleForm[str].match(/^[0-9]*.[0-9]{0,4}/)[0]
          } else {
            var tibi_fee = 1 - this.coinDetail.currency_tibi_fee / 100

            // start 以下计算方式防止小数相乘精度丢失
            let m = 0
            const s1 = this.ruleForm.turn_out_num.toString()
            const s2 = tibi_fee.toString()
            m += s1.split('.')[1].length
            m += s2.split('.')[1].length
            if (this.coinDetail.fee_tibi_mod === 2) {
              this.ruleForm.turn_out_account = this.ruleForm.turn_out_num - this.coinDetail.currency_tibi_fee
            } else {
              this.ruleForm.turn_out_account = (Number(s1.replace('.', ' ')) * Number(s2.replace('.', ' ')) / Math.pow(10, m)).toFixed(8)
            }
          }
        }
      },
      getUserInfo() {
        checkToken().then(res => {
          if (res.status !== 1000) {
            this.$message.error(res.msg)
            return false
          }
          if (res.data.is_set_double_certification === 1) {
            this.is_set_double_certification = true
          } else {
            this.ruleForm.pass_double = '无'
            this.is_set_double_certification = false
          }
          console.log(res.data)
        }).catch(() => {
        })
      },
      setTime() {
        let time = 60
        if (this.validCode) {
          this.validCode = false
          const _this = this
          this.timeInterval = setInterval(function() {
            time--
            _this.validTip = time + _this.$t('verifyCode.second')
            if (time === 0) {
              clearInterval(_this.timeInterval)
              _this.validTip = _this.$t('verifyCode.getAgain')
              _this.validCode = true
              _this.validState = true
            }
          }, 1000)
          return this.timeInterval
        }
      },
      getPhoneNum() {
        this.link_userInfo = window.localStorage.getItem('link_userInfo')
        this.link_userInfo = JSON.parse(this.link_userInfo)
        console.log(this.link_userInfo)
        if (this.link_userInfo.phone && this.link_userInfo.phone !== '0') {
          this.phone = this.link_userInfo.phone
        } else {
          this.$confirm(this.$t('errorTips.withoutBindPhone'), this.$t('dialog.title'), {
            confirmButtonText: this.$t('dialog.sure'),
            cancelButtonText: this.$t('dialog.cancle'),
            type: 'warning'
          }).then(() => {
            this.$router.push('/user/account')
          }).catch(() => {
          })
        }
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (!this.validState) {
            return false
          }
          if (valid) {
            const data = { ...this.ruleForm }
            if (!this.is_set_double_certification) {
              delete data['pass_double']
            }
            data.currency_id = this.selectCoinID
            data.trade_password = md5(data.trade_password)
            memberAssetOutTo(data).then(res => {
              console.log('res', res)
              if (res.status !== 1000) {
                this.$message.error(res.msg)
                return false
              }
              this.validState = false
              this.$message({
                type: 'success',
                message: '转出成功！'
              })
              this.validTip = this.$t('verifyCode.title')
              this.validCode = true
              this.validState = true
              clearInterval(this.timeInterval)
              // 刷新表格
              this.getTransferOutList()
              this.getAssetTurnInTo()
            }).catch(() => {
            })
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      resetForm(formName) {
        this.$refs[formName].resetFields()
      },
      getTransferOutList() {
        // {...this.pageOptions}
        const data = {
          'length': this.pageOptions.per_page_num,
          'start': (this.pageOptions.page - 1) * 10,
          'status': -1
        }
        if (this.selectCoinID) {
          data.currency_id = this.selectCoinID
        }
        assetTurnOutToList(data).then(res => {
          if (res.status !== 1000) {
            this.$message.error(res.msg)
          }
          this.tableData = [...res.data.tibi_list]
          this.totalCount = res.recordsTotal
        }).catch(() => {
        })
      },
      sendRequestSms(event) {
        const data = {}
        if (this.phone) {
          this.setTime()
          data.user_account = this.phone
          data.request_type = 'changeout'
          sendRequestSms(data).then(res => {
            console.log('res', res)
            if (res.status !== 1000) {
              this.$message.error(res.msg)
              return false
            }
          }).catch(() => {
          })
        } else {
          this.$message.alert(this.$t('errorTips.withoutBindPhone'), '', {
            confirmButtonText: this.$t('dialog.sure'),
            center: true,
            showClose: false,
            callback: action => {
            }
          })
        }
      }
    }
  }
</script>
<style lang="less" scoped>
  .plant-change{
    background: white;
    font-size: 14px;
    color: #3e4558;
    &.content {
      min-height: 770px;
      padding: 20px 25px;
    }
    .gray{
      color: #9397a0;
    }
    .swicth-btn a.normal {
      color: #7d58d1;
    }
    .swicth-btn a {
      margin-right: 0 !important;
    }

    .swicth-btn a {
      color: #7d58d1;
      display: inline-block;
      padding: 3px 12px;
      margin-right: 18px;
      border: 1px solid #7d58d1;
      border-radius: 5px;
      font-size: 12px;
      }
     .title {
        padding-top: 9px;
        padding-bottom: 18px;
        border-bottom: 1px solid #e6eaf0;
        font-size: 16px;
      }
  .invite-rules{
    margin: 44px 0 27px 0;
    font-size: 12px;
    div{
      margin: 10px 0;
    }
    p{
      padding-bottom: 10px;
    }
  }
    .can-use{
      font-size: 12px;
      color: #909399;
      line-height: 16px;
      margin-top: 5px;
    }
    .currency_mark {
      display: inline-block;
      margin: 0 0px 0 30px;
      line-height: 37px;
      width: 250px;
    }
    .content-pad {
      padding: 32px 0px 22px;
      height: 37px;
      line-height: 37px;
      border-bottom: 1px dashed #e6eaf0;
    }
    .plant-form{
      margin: 40px 0  0 130px;
      width: 390px;
    }
    .verify .el-input__inner,
    .verify .el-input{
      width: 149px;
    }
    .get-verify {
      width: 100px;
      margin-left: 10px;
      display: inline-block;
      padding: 12px 13px;
      background: rgba(31,168,246,.15);
      color: rgba(31, 168, 246, 0.7);
    }
    .sure-change{
      padding: 12px 100px;
    }
     /deep/ .el-form--label-left .el-form-item__label{
      text-align: right;
    }
    /deep/ .el-form-item__label{
      // margin: 0 20px 0 0;
    }
  }
</style>
<style lang="less" >
  .plant-form{
    .el-input--suffix .el-input__inner{
      width: 260px;
    }
    .el-radio-button{
      width: 130px;
    }
    .el-radio-group{
      width: 260px;
    }
    .el-radio-button__inner{
      width: 130px;
    }
  }
</style>


